import { SharedComponents } from "src/app/shared.components";
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AlertService } from "src/services/alert.service";
import { ReportService } from "src/services/report.service";
import { ITransaction } from "src/interfaces/i.transaction.interface";

@Component({
  selector: 'app-details-freight',
  templateUrl: './freight-details.component.html',
  styles: [
    `
.mt-container    {
      height: auto;
    }
    .no-borader>tbody>tr>td{
      border:none !important
    }

    h6{
      display: inline-block;
      color:#1b55e2;
      font-weight: 700;
    font-size: 13px;
    letter-spacing: 1px;
    text-transform: uppercase;
    }

    h6:after{
      content:':'
    }
    `
  ]
})
export class FerightDetailsComponent extends SharedComponents implements OnInit {
  freight: ITransaction ={} as ITransaction;
  isRequestingToServer:boolean=false;
  constructor(public service:ReportService, private activateRouter: ActivatedRoute) { super() }

  ngOnInit(): void {
    this.service.seo.updateSEO("freightDetils");
    //Get Detail From The Serer
    this.getFreightDetails();
  }

  /**
   * Get Details
   */
  getFreightDetails() {
    this.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.service.http.getFreightDetails<ITransaction>(this.activateRouter.snapshot.params.targetId)
        .subscribe(res => {
          this.isRequestingToServer = false;
          if (res.isSuccess) {
            this.freight = res.result;
          } else {
            AlertService.showResponseMessage(res);
          }
        }, error => {
          this.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }
}//End Class
