import { FerightDetailsComponent } from './../app/reports/freight-details/freight-details.component';
import { ClinetFerightDetailsComponent } from './../app/reports/clinet-freight-details/clinet-freight-details.component';
import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { ClinetFreightsReportComponent } from "src/app/reports/clinet-freights-report/clinet.freights.report.component";
import { ContainersReportComponent } from "src/app/reports/containers-report/containers.report.component";
import { AdminAuthCanActive } from "src/services/authentcation/admin.auth.can.active.service";
import { ClinetAuthCanActive } from "src/services/authentcation/clinet.auth.can.active.service";
import { UserAuthCanActive } from "src/services/authentcation/user.auth.can.active.service";

const ROUTS: Routes = [
    { path: 'containers', component: ContainersReportComponent, canActivate: [AdminAuthCanActive] },
    { path: 'freights', component: ClinetFreightsReportComponent, canActivate: [ClinetAuthCanActive] },
    { path: 'freights/details/:targetId', component: ClinetFerightDetailsComponent, canActivate: [ClinetAuthCanActive] },
    { path: 'freights/fullDetails/:targetId', component: FerightDetailsComponent, canActivate: [AdminAuthCanActive] },


];

@NgModule({
    imports: [RouterModule.forChild(ROUTS)],
    exports: [RouterModule]
})
export class ReportsRoutingModule {

}

